import React, { Component } from "react";



export default class Login extends Component {
 constructor(props) {
  super(props)
  this.state = {
   email: {'value':'','error':''},
   password: {'value':'','error':''},
  }
  this.handleChange = this.handleChange.bind(this)
  this.handleSubmit = this.handleSubmit.bind(this)
  console.log(this.props);
 }

 handleChange = (e) => {
  let newState = {}
  newState[e.target.name] = {}
  newState[e.target.name]['value'] = e.target.value
  newState[e.target.name]['error'] = this.state[e.target.name]['error']
  this.setState(newState)
 }

 handleSubmit = (e, message) => {
  e.preventDefault()

  let formData = {
   email: this.state.email.value,
   password:this.state.password.value,
  }

  if (formData.email.length < 1 || formData.password.length < 1) {
   return false
  }

  $.ajax({
   url: 'http://localhost:8000/accounts/login/',
   dataType: 'json',
   type: 'POST',
   data: formData,
   success: function(data) {
       console.log(this.props)
      this.props.history.push('/tickets')
    }.bind(this),
   error: function(xhr, status, err) {
        console.error(status, err.toString())
        alert('Please Provide valid credentials')
   }.bind(this)
  })

  this.setState({
   email: {'value':'',error:''},
   password:{'value':'',error:''}
  })
 }


    render() {
        return (
              <div className="auth-wrapper">
                <div className="auth-inner">
            <form onSubmit={this.handleSubmit}>
                <h3>Sign In</h3>

                <div className="form-group">
                    <label>Email address</label>
                    <input type="email" name="email"  onChange={this.handleChange} value={this.state.email.value} className="form-control" placeholder="Enter email" />
                    <div className="invalid-feedback">{this.state.email.error}</div>
                </div>

                <div className="form-group">
                    <label>Password</label>
                    <input type="password" name="password"  onChange={this.handleChange} value={this.state.password.value} className="form-control" placeholder="Enter password" />
                    <div className="invalid-feedback">{this.state.password.error}</div>
                </div>

                <button type="submit" className="btn btn-primary btn-block">Submit</button>
            </form></div></div>
        );
    }
}