import React from 'react';
import '../../../node_modules/bootstrap/dist/css/bootstrap.css';
import './App.css';
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

import Login from "../../components/login_component";
import ManageTickets from "../../components/manage_tickets";

function App() {
const styles = {
  container: {
   display: 'inline',
  }
}

  return (<Router>
    <div className="App">
      <nav className="navbar navbar-expand-lg navbar-light fixed-top">
        <div className="container">
          <Link className="navbar-brand" to={"#"}>PeerXP in React</Link>
          <div className="collapse navbar-collapse" id="navbarTogglerDemo02">
            <ul className="navbar-nav ml-auto">
              <li className="nav-item" style={{display:"inline"}}>
                <Link className="nav-link" className="container" style={styles.container} to={"/login"}>Login</Link>
                <Link className="nav-link" className="container" style={styles.container} to={"/logout"}>Logout</Link>
                <Link className="nav-link" className="container" style={styles.container} to={"/tickets"}>Tickets</Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      <Switch>
        <Route exact path='/login' component={Login} />
        <Route path="/tickets" component={ManageTickets} />
        <Route path="/logout" component={Login} />
      </Switch>
    </div></Router>
  );
}

export default App;