import React, { Component } from "react";
import BootstrapTable from '../../node_modules/react-bootstrap-table-next';

export default class ManageTickets extends Component {
 constructor(props) {
  super(props)
  this.state = {

  }
 }

    componentDidMount(){ this._updateState() }
  componentWillReceiveProps(){ this._updateState() }

  _updateState(){
    $.get('http://localhost:8000/tickets/list/', (data)=>{
      this.setState({data:data})
    })
  }

    render() {
        const products = [{'id':1,'name':'sdasda','price':1212},{'id':1,'name':'sdasda','price':1212}];
        const columns = [{
          dataField: 'id',
          text: 'Product ID'
        }, {
          dataField: 'name',
          text: 'Product Name'
        }, {
          dataField: 'price',
          text: 'Product Price'
        }];

        return (<div style={{marginTop: 10 + 'em'}}><BootstrapTable  keyField='id' data={ products } columns={ columns } /></div>);
    }
}